import { useState } from "react";
import * as S from "./style";
import Row from "../components/atoms/row";

import Paging from "../components/common/Pagination";

import { ReactComponent as Search } from "../svg/Search.svg";
import { ReactComponent as Memo } from "../svg/memo.svg";
import { useNavigate } from "react-router-dom";
import Header from "../components/common/header";
import MemoModal from "../components/common/modal/memo";

const ManagementCafe = ({ setSubText }) => {
  const [isActive, setIsActive] = useState(1);
  const [modal, setModal] = useState(false);
  const navigate = useNavigate();
  const temp = [
    {
      code: "000001",
      name: " ",
      location: " ",
      phoneNum: "0-1234-2345",
      congestion: "",
      review: "245",
      registration: "03/29/2022",
      edited: "03/29/2022",
      memo: "",
      img: "/.PNG",
    },

    {
      code: "000001",
      name: " ",
      location: " ddddddddddddd",
      phoneNum: "0-1234-2345",
      congestion: "",
      review: "245",
      registration: "03/29/2022",
      edited: "03/29/2022",
      memo: "",
      img: null,
    },
    {
      code: "000001",
      name: " ",
      location: " ",
      phoneNum: "0-1234-2345",
      congestion: "",
      review: "245",
      registration: "03/29/2022",
      edited: "03/29/2022",
      memo: "",
      img: null,
    },
    {
      code: "000001",
      name: " ",
      location: " kkkkk",
      phoneNum: "0-1234-2345",
      congestion: "",
      review: "245",
      registration: "03/29/2022",
      edited: "03/29/2022",
      memo: "",
      img: null,
    },
  ];

  const [search, setSearch] = useState("");

  const [page, setPage] = useState(1);
  const [count, setCount] = useState(temp.length * 3 - 1);
  const [items, setItems] = useState(9);
  const handlePageChange = (page) => {
    setPage(page);
    console.log(page);
  };

  return (
    <>
      <Header mcolor={"#fff"} text={" "} subText={"  00"} />
      <Row
        justify={"space-between"}
        align={"baseline"}
        style={{ marginBottom: "20px" }}
      >
        <Row gap={15}>
          <S.Sbtn
            onClick={() => {
              navigate("/management/register");
              setSubText("  ");
            }}
            isTrue={isActive === 1}
          >
              
          </S.Sbtn>
          <S.Sbtn onClick={() => setIsActive(2)} isTrue={isActive === 2}>
            
          </S.Sbtn>
          <S.Sbtn onClick={() => setIsActive(3)} isTrue={isActive === 3}>
             
          </S.Sbtn>
        </Row>
        <Row gap={15} align={"baseline"}>
          <Paging
            count={count}
            handlePageChange={handlePageChange}
            setPage={setPage}
            page={page}
          />
          <S.Sbtn></S.Sbtn>
          <Row style={{ borderBottom: "1px solid #fff" }}>
            <S.Input
              placeholder=""
              type="text"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <Search onClick={onclick} />
          </Row>
        </Row>
      </Row>
      <S.Wrapper>
        <S.TableHeader>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td> </td>
            <td></td>
          </tr>

          {temp
            .concat(temp)
            .concat(temp)
            .slice(items * (page - 1), items * (page - 1) + items)
            .map((item) => (
              <tr height="72px">
                <td>
                  <div>{item.code}</div>
                </td>
                <td>
                  <Row gap={16} align={"center"}>
                    {item.img ? (
                      <S.Photo>
                        z
                        <img
                          src={process.env.PUBLIC_URL + item.img}
                          alt="pic"
                        />
                      </S.Photo>
                    ) : (
                      <S.NonePic> </S.NonePic>
                    )}
                    <p>{item.name}</p>
                  </Row>
                </td>
                <td>{item.location}</td>
                <td>{item.phoneNum}</td>
                <td>{item.congestion}</td>
                <td>{item.review}</td>
                <td>{item.registration}</td>
                <td>{item.edited}</td>
                <td>
                  <Memo onClick={() => setModal(true)} />
                </td>
              </tr>
            ))}
        </S.TableHeader>
      </S.Wrapper>
      {modal && <MemoModal setModal={setModal} />}
    </>
  );
};

export default ManagementCafe;
